import { createApp } from 'vue';
import App from './App.vue';
import router from './router';
import authService from './services/auth'; // Importamos el servicio

// 1. Importa nuestro sistema de diseo global
import '@/assets/styles/main.css';

// 2. Importa libreras de terceros
import 'bootstrap/dist/css/bootstrap.min.css';
import '@fortawesome/fontawesome-free/css/all.css';

// 3. Importa plugins de Vue
import { createVfm } from 'vue-final-modal';
import 'vue-final-modal/style.css'

// Creamos una funcin asncrona para controlar el arranque
async function startApp() {
  // Primero, intentamos restaurar la sesin del usuario.
  // La aplicacin esperar aqu hasta que checkAuth() termine.
  await authService.checkAuth();

  const vfm = createVfm();
  const app = createApp(App);

  app.use(router);
  app.use(vfm);

  // Solo montamos la aplicacin cuando la comprobacin ha finalizado.
  app.mount('#app');
}

// Llamamos a la funcin para iniciar la aplicacin.
startApp();